import React from 'react';
import '../../index.scss';

import { FiSend } from "react-icons/fi";



export default class Form extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: "",    
     
    };
  }




  
  render() {
    const { status } = this.state;
    return (
    <div className="form" style={ this.props.passStyle ? {display:'flex'} : {display:'none'} }> 

      
        <div className="cross" onClick={this.props.action}>
            <div className="lineCross"></div>
            <div className="lineCross"></div>
        </div>

        
        <form
            onSubmit={this.submitForm}
            action="https://formspree.io/myyyobby"
            method="POST"
        >
        
            <label>Email:</label>
            <input type="email" name="email" />
            <label>Message:</label>
            <textarea type="text" name="message" style={{height:"330px"}}/>
            {status === "SUCCESS" ? <p>Thanks! <FiSend style={ {color:"rgb(25, 210, 148)", fontSize:"1.3em"}}/> </p> : <button>Submit</button>}
            {status === "ERROR" && <p>Ooops! There was an error.</p>}
        </form>

        <div className="right">
          <div className="phone">
                  <h3>Call me</h3>
                  <a href="tel:+48661047271" rel="noopener noreferrer"> <p>661-047-271</p> </a>
              </div>
             
              <div className="mail">
                  <h3>Message me</h3>
                  <a href="mailto:domaradzkicode@gmail.com" email="true" rel="noopener noreferrer"> <p>domaradzkicode@gmail.com</p> </a>
              </div>
        </div>
      </div>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}