import React from 'react';
import '../../../index.scss';
import { IoLogoGithub } from "react-icons/io";
import { IoIosLink } from "react-icons/io";

const Item = ({title,text,image,linkHub,link}) => (
    <div className="project-item">
    
        <div className="overlay" >
         <div className="image" style={{backgroundImage: `url(${image})` }}/>
        </div>
       
         <div className="icons">
            <div className="icon"> <a href={`${linkHub}`} target="_blank" rel="noopener noreferrer"> <IoLogoGithub /> </a> </div>
            <div className="icon"> <a href={`${link}`} target="_blank" rel="noopener noreferrer"> <IoIosLink /> </a> </div>
        </div>
        
       
        <div className="description">
            <h3>{title}</h3>
            <p>{text}</p>
        </div>
        <div className="tech-stack">
            <div>JS</div>
            <div>CSS</div>
            <div>React</div>
        </div>
    </div>
)

export default Item;