import React from 'react';
import Item from './item/item.component';
import '../../index.scss';
const ItemsShowcase = ({items}) => (
    <div className="items-showcase__wrapper">
    {
        items.map(({id, ...otherProps})=>(
            <Item key={id}  {...otherProps} />
        ))
    }
       
        

    </div>
)

export default ItemsShowcase;